  methods: {
    SetAllHsId(ele) {
      /**è·å–eleå…ƒç´ é‡Œé¢çš„hæ ‡ç­¾ */
      if (ele == null) return;
      let sons = ele.getElementsByTagName("*");
      let hsArray = [];
      sons.forEach((element) => {
        let tagName = element.nodeName;
        if (tagName[0] === "H") {
          hsArray.push(element);
        }
      });

      hsArray.forEach((v) => {
        v.setAttribute("id", v.innerText);
      });
    },
    /**
