<template>
  <div id="articleTitleList">
    <strong>ç›®å½•</strong>
    <nav>
      <li v-for="(value, index) of titles" :key="index">
        <a :href="'#'+value.content" :title="value.content">{{value.content}}</a>
      </li>
    </nav>
  </div>
</template>
<script>


import { ParseArticleContentToHs } from "@/utils/ArticleParse.js";

export default {
  name:'ArticleTitleList',
  components:{},
  model:{},
  props:{

  },
  data(){
    return {
      fn:null,
      titles:[],
    }
  },
  computed:{},
  watch:{},
  created(){
  },
  mounted(){
    setTimeout(() => {
      this.titles = ParseArticleContentToHs(document.getElementById("_articleHTML"))
    }, 100);
    this.fn = this.SetTitleNavTop(this.$el);
    window.addEventListener("scroll", this.fn, false);
  },
  updated(){
  },
  beforeDestroy(){
    window.removeEventListener("scroll", this.fn);
  },
  methods:{
    /**
     * å½“æ»šåŠ¨æ¡ä¸¾ä¾‹é¡¶ç‚¹çš„è·ç¦»è¶…è¿‡78çš„æ—¶å€™ï¼Œé‚£ä¹ˆå°±å°†ç»„ä»¶çš„topå€¼è®¾ç½®ä¸º0ï¼Œæ€»ä½“æ•ˆæœä¸ºç²˜æ€§å¸ƒå±€
     */
    SetTitleNavTop(el) {
      return function(){
        if(el === undefined || el === null) return;
        let articleH = 86;
        const top =
          window.pageYOffset ||
          document.documentElement.scrollTop ||
          document.body.scrollTop; //å…¼å®¹ä¸åŒçš„æµè§ˆå™¨

        if (top > articleH) {
          el.style["top"] = 0;
        } else {
          let hight = articleH - top;
          el.style["top"] = hight + "px";
        }
      }
    },
  },
};
</script>
<style lang="less">
#articleTitleList {
    position: fixed;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    background:white;
    padding:10px ;
    top:86px;
    width: 13vw;
    li {
      line-height: 30px;
      font-size: 15px;
    }
}
</style>