export function httpCommentAdd(aId, uId, userName, content, path, pId) {

    if (validateLogin() === false) {
        Message({
            message: "è¯·æ‚¨ç™»å½•",
            type: 'error'
        });
        return Promise.reject();
    };

    return instance.request({
        url: '/comment/add',
        method: 'post', // default
        data: {
            aId,
            uId,
            userName,
            content,
            path,
            pId
        },
    });
}


/**
 * æ ¹æ®æ–‡ç« IDè·å–æ‰€æœ‰æ¶ˆæ¯
 */
export async function httpCommentQuery(aId) {

    return instance.request({
        url: '/comment/queryAll',
        method: 'get', // default
        params: {
            aId: aId
        },
    });

    let msg = {};
    try {
        let {
            data
        } = await instance.request({
            url: '/comment/queryAll',
            method: 'get', // default
            params: {
                aId: aId
            },
        });
        msg = data;
    } catch {

    }

    return msg;
}