<template>
  <div class="ReadArticle">
    <el-row :gutter="20">
      <el-col :span="2" :offset="2">
        <!-- è§‚çœ‹ ç‚¹èµ æ”¶è— å®¡æ ¸-->
        <div class="handle-wrapper">
          <el-button
            v-if="$store.state.people.user && $store.state.people.user.authority === 1"
            @click="canCross"
            type="success"
            style="margin-bottom:20px"
